  .iphonex {
    height: 500px;
    width: 240px;
    position: relative;
  }
  
  .iphonex__case {
    position: absolute;
    background: transparent;
    width: 235px;
    height: 475px;
    border: 11px solid #151515;
    box-sizing: border-box;
    border-radius: 35px;
    left: 3px;
  }
  
  .iphonex__top {
    position: absolute;
    background: black;
    height: 20px;
    width: 213px;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
  }
  
  .iphonex__time {
    position: absolute;
    top: 3px;
    left: 14px;
    font-size: 10px;
    font-weight: bold;
    font-family: 'Roboto';
    color: white;
  }
  .iphonex__wifi {
    position: absolute;
    right: 25px;
    font-size: 10px;
    top: 3px;
  }
  .iphonex__battery {
    position: absolute;
    right: 8px;
    font-size: 10px;
    top: 3px;
  }
  
  .iphonex__notch {
    background: #151515;
    height: 15px;
    width: 115px;
    position: absolute;
    left: 48px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  
  .iphonex__speaker {
    background: rgb(51, 51, 51);
    width: 30px;
    height: 6px;
    border-radius: 8px;
    position: absolute;
    left: 43px;
  }
  
  .iphonex__camera {
    background: rgb(51, 51, 51);
    width: 7px;
    height: 7px;
    border-radius: 8px;
    position: absolute;
    left: 80px;
  }
  
  .iphonex__screen {
    width: 214px;
    height: 435px;
    background-image: url('/src/Images/IphoneBackground.jpg');
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    top: 20px;
    position: absolute;
  }
  
  .iphonex__power {
    width: 3px;
    height: 19px;
    background: rgb(0, 0, 0);
    position: absolute;
    left: 0;
    top: 62px;
  }
  .iphonex__volume {
    width: 3px;
    height: 32px;
    background: rgb(0, 0, 0);
    position: absolute;
    left: 0px;
    top: 98px;
  }
  .iphonex__volume--down {
    top: 142px;
  }
  
